body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FFE600;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.code-scanner__input {
  border: 0;
  width: 1px;
  height: 1px;
  padding: 0;
  position: fixed;
  z-index: -100;
  opacity: 0;
}

h1 {
  text-align: center;
}

.alert {
  margin-top: 1.2em;
  width: 4em;
  margin-left: 1em;
}
.page {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  min-height: 100vh;
}
.floating-menu {
  position: relative;
  margin: 0.8em;
}

.list {
  -webkit-font-smoothing: antialiased;
  background-color: #fff;
  font-size: 18px;
  font-weight: 400;
  line-height: 1;
  margin: 0;
  padding: 0;
  border-radius: 10px;
}

.list__item {
  display: flex;
  justify-content: space-between;
  padding: 20px;
}